import React, { useState } from 'react';
import axios from "axios";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";


import Check from "@material-ui/icons/Check";
// core components
import SnackbarContent from "components/Snackbar/SnackbarContent.js";


import styles from "assets/jss/material-kit-react/views/loginPage.js";
import "./login.css"
import "./register.scss"


const placeorder = "https://harrymettle21.herokuapp.com/register";

const useStyles = makeStyles(styles);

export default function LoginPage(props) {
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  setTimeout(function() {
    setCardAnimation("");
  }, 700);
  const classes = useStyles();
  const { ...rest } = props;
  const [data, setData] = useState({
		name: "",
		email: "",
    phone: "",
    college: "",
  });
  const [ isSuccessfullySubmitted, setIsSuccessfullySubmitted] = React.useState(false);

  const submit = (e) => {
		e.preventDefault();
    console.log(data);
      register(data);
      setData({...data, name: "",
      email: "",
      phone: "",
      college: "", });
      setIsSuccessfullySubmitted(true);

 
  };
  const register = (data) => {
    axios
      .post(`${placeorder}`, data)
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };
  
  return (
    <div className="reglog">
      <div id="stars"></div>
      <div id="stars2"></div>
      <div id="stars3"></div>
      <Header absolute color="transparent" brand="Mettle21" rightLinks={<HeaderLinks />} {...rest}/>
      <div className={classes.pageHeader} > 
      <div className={classes.container}>
      {isSuccessfullySubmitted && (<SnackbarContent
        message={
          <span>
            <b>SUCCESS :</b> You{"'"}ve successfully registered for METTLE21 :)
          </span>
        }
        close
        color="success"
        icon={Check}
      />)}
          <GridContainer justify="center">
          

            <GridItem xs={12} sm={9} md={5}>
              <Card className={classes[cardAnimaton]} style={{borderRadius:"25px"}}>
                
                  <CardHeader color="primary" className={classes.cardHeader} style={{borderRadius:"25px"}}>
                    <h3>Register</h3>
                    <div className={classes.socialLine}>
                      <Button justIcon  target="_blank" color="transparent" onClick={e => e.preventDefault()} >
                        <i className={"fas fa-user"} />
                        </Button>
                      <Button justIcon  target="_blank" color="transparent" onClick={e => e.preventDefault()}>
                        <i className={"fas fa-envelope"} />
                      </Button>
                      <Button justIcon  target="_blank" color="transparent" onClick={e => e.preventDefault()}>
                        <i className={"fas fa-phone"} />
                      </Button>
                      <Button justIcon  target="_blank" color="transparent" onClick={e => e.preventDefault()}>
                        <i className={"fas fa-graduation-cap"} />
                      </Button>
                    </div>
                  </CardHeader>
                  <CardBody>
                  <form onSubmit={submit} >
                    <div className="txt_field">
                        <input type="text"  id="name" required
                         value={data.name} onChange={(val) => setData({...data, name: val.target.value, })}/>
                          <span></span><label>Name...</label>
                      </div>
                      <div className="txt_field">
                        <input type="email"  id="email" required 
                        value={data.email} onChange={(val) => setData({...data, email: val.target.value, })}/>
                          <span></span><label>Email...</label>
                      </div>
                      <div className="txt_field">
                        <input type="number"  id="phone" required
                         value={data.phone} onChange={(val) => setData({...data, phone: val.target.value, })}/>
                          <span></span><label>Phone...</label>
                      </div>
                      <div className="txt_field">
                        <input type="text"  id="college" required 
                        value={data.college} onChange={(val) => setData({...data, college: val.target.value, })}/>
                          <span></span><label>College...</label>
                      </div>
                    
                    
                   <center><Button type="submit" round color="primary" size="md" >
                      Get started
                    </Button></center>
                    
                    </form>
                  </CardBody>
                  <CardFooter className={classes.cardFooter}>
                    
                  </CardFooter>
                
              </Card>
            </GridItem>
          </GridContainer>
        </div>
        <Footer whiteFont />
      </div>
    </div>
  );
}
