import React from "react";
// react component for creating beautiful carousel
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";

import image1 from "assets/img/images/art/Blog1.jpeg";
import image2 from "assets/img/images/art/Blog2.jpg";
import image3 from "assets/img/images/art/Blog3.png";
import image4 from "assets/img/images/img_alter/blog4.png";
import image5 from "assets/img/images/img_alter/blog5.JPG";


import styles from "assets/jss/material-kit-react/views/componentsSections/carouselStyle.js";
import "./Sectioncar.scss";
const useStyles = makeStyles(styles);





const slides = [
    {
      title: "",
      subtitle: "Metals and Materials: Pillars of Independence",
      description: "",
      image: image2,
      linkt:"https://medium.com/mettle-nit-trichy/metals-and-materials-pillars-of-independence-ac0acb9e54df"

    },
    {
      title: "",
      subtitle: "Carbon Fibers Reinforced Plastics",
      description: "",
      image:image1,
      linkt:"https://medium.com/mettle-nit-trichy/carbon-fibers-reinforced-plastics-4dab9d06be16"

    },
    {
      title: "",
      subtitle: "Cloths that change temperature!",
      description: "",
      image:image3,
      linkt:"https://medium.com/mettle-nit-trichy/cloths-that-change-temperature-1184295ceaa6"

   },
    {
      title: "",
      subtitle: "Nanoscience is the future of technology",
      description: "",
      image:image4,
      linkt:"https://medium.com/mettle-nit-trichy/nanoscience-is-the-future-of-technology-905bf8a2dc7e"

    },
    {
      title: "",
      subtitle: "ML and Data Science: Empowering Materials Science",
      description: "",
      image:image5,
      linkt:"https://medium.com/mettle-nit-trichy/ml-and-data-science-empowering-materials-science-64837fa3cdd2"

    },
 
  ];
  
  function useTilt(active) {
    const ref = React.useRef(null);
  
    React.useEffect(() => {
      if (!ref.current || !active) {
        return;
      }
  
      const state = {
        rect: undefined,
        mouseX: undefined,
        mouseY: undefined
      };
  
      let el = ref.current;
  
      const handleMouseMove = (e) => {
        if (!el) {
          return;
        }
        if (!state.rect) {
          state.rect = el.getBoundingClientRect();
        }
        state.mouseX = e.clientX;
        state.mouseY = e.clientY;
        const px = (state.mouseX - state.rect.left) / state.rect.width;
        const py = (state.mouseY - state.rect.top) / state.rect.height;
  
        el.style.setProperty("--px", px);
        el.style.setProperty("--py", py);
      };
  
      el.addEventListener("mousemove", handleMouseMove);
  
      return () => {
        el.removeEventListener("mousemove", handleMouseMove);
      };
    }, [active]);
  
    return ref;
  }
  
  const initialState = {
    slideIndex: 0
  };
  
  const slidesReducer = (state, event) => {
    if (event.type === "NEXT") {
      return {
        ...state,
        slideIndex: (state.slideIndex + 1) % slides.length
      };
    }
    if (event.type === "PREV") {
      return {
        ...state,
        slideIndex:
          state.slideIndex === 0 ? slides.length - 1 : state.slideIndex - 1
      };
    }
  };
  
  function Slide({ slide, offset }) {
    const active = offset === 0 ? true : null;
    const ref = useTilt(active);
  
    return (
      <div
        ref={ref}
        className="slide"
        data-active={active}
        style={{
          "--offset": offset,
          "--dir": offset === 0 ? 0 : offset > 0 ? 1 : -1
        }}
      >
        
        <div
          className="slideContent"
          style={{
            backgroundImage: `url('${slide.image}')`
          }}
        >
          <div className="slideContentInner">
            <h2 className="slideTitle">{slide.title}</h2>
            <h3 className="slideSubtitle"><a href={slide.linkt} target="_black" style={{color:"white"}}>{slide.subtitle}</a></h3>
            <p className="slideDescription">{slide.description}</p>
          </div>
        </div>
      </div>
    );
  }
  
  export default function Sectioncar() {
    const classes = useStyles();
    const [state, dispatch] = React.useReducer(slidesReducer, initialState);
  
    return (
        <div className={classes.section}>
        <GridContainer>
          <GridItem xs={12} sm={12} md={12} className={classes.marginAuto}>
              <Card carousel>
                <div className="bacblack">
                    <div className="car">
                        <div className="rac">
                            <div className="slides">
                                <button onClick={() => dispatch({ type: "PREV" })}>‹</button>
                        
                                {[...slides, ...slides, ...slides].map((slide, i) => {
                                let offset = slides.length + (state.slideIndex - i);
                                return <Slide slide={slide} offset={offset} key={i} />;
                                })}
                                <button onClick={() => dispatch({ type: "NEXT" })}>›</button>
                            </div>

                        </div>
                    </div>
                </div>
            </Card>
      </GridItem>
        </GridContainer>
    </div>
  );
  }
  

  