import React from "react";
// plugin that creates slider
import Slider from "nouislider";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import { Link } from "react-router-dom";
import Button from "components/CustomButtons/Button.js";


import styles from "assets/jss/material-kit-react/views/componentsSections/basicsStyle.js";
import "./sectionbasic.css"

const useStyles = makeStyles(styles);

export default function SectionBasics() {
  const classes = useStyles();

  React.useEffect(() => {
    
    if (
      !document.getElementById("sliderDouble").classList.contains("noUi-target")
    ) {
      Slider.create(document.getElementById("sliderDouble"), {
        start: [10, 90],
        connect: [false, true, false],
        step: 1,
        range: { min: 0, max: 100 }
      });
    }
    return function cleanup() {};
  });

  return (
   
    <div className={classes.sections} >
      <div className={classes.container}>
        <div className={classes.title}>
          <div class="bubble wow fadeInUp" data-wow-offset="200" data-wow-duration=".5s" style={{visibility: "visible", animationDuration: "0.5s"}}>
          <center><h2>About Us</h2></center>
          <h5>Mettle is the Annual National Level Technical Symposium conducted by the department of Metallurgical and Materials Engineering, NIT Trichy. With an aim to recognize and highlight the growing role of Metallurgical and Materials Engineering in our technological applications, Mettle serves as a common platform and hosts a series of activites like guest lectures, workshops, events , demonstrations for the benefit of community and better technological cohesivity within people of various walks in materials engineering. Come and witness a truly inspiring happenings at NIT Trichy.</h5>
          </div>
        </div>
        
        <div className={classes.space50} />        

        
        <div id="sliders">
          <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
              <div id="sliderDouble" className="slider-primary" />
            </GridItem>
           
          </GridContainer>
          <div className={classes.space50} />  
          <div>
          <center>
          <h4>Register here to participate in Campus Ambassador Programme!</h4>
              
            <Link to="/campusAmbassador">
              <Button
                color="success"
                round
                size="lg"
                href=""
                target="_blank"
                rel="noopener noreferrer"
              >
                <i className="fas fa-id-card" />
                Campus Ambassador
              </Button>
              </Link>
              </center>
              </div>
            
        </div>
      </div>
    </div>

  );
}
