import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch } from "react-router-dom";

import "assets/scss/material-kit-react.scss?v=1.9.0";

// pages for this product
import Components from "views/Components/Components.js";
import LandingPage from "views/LandingPage/LandingPage.js";
import ProfilePage from "views/ProfilePage/ProfilePage.js";
import WorkshopsPage from "views/workshopsPage/WorkshopsPage.js";
import guestlecturePage from "views/guestlecturePage/guestlecturePage.js";
import campusamb from "views/campusamb/campusamb.js";
import LoginPage from "views/LoginPage/LoginPage.js";

var hist = createBrowserHistory();

ReactDOM.render(
  <Router history={hist}>
    <Switch>
      <Route path="/events" component={LandingPage} />
      <Route path="/workshops" component={WorkshopsPage} />
      <Route path="/guestlectures" component={guestlecturePage} />
      <Route path="/campusAmbassador" component={campusamb} />
      <Route path="/profile-page" component={ProfilePage} />
      <Route path="/register" component={LoginPage} />
      <Route path="/" component={Components} />
    </Switch>
  </Router>,
  document.getElementById("root")
);
